import {useState} from "react";

import {ethers} from "ethers";

import 'reset-css';
import 'bootstrap/dist/css/bootstrap.min.css';

import {Button, Container} from "react-bootstrap";
import {WalletTable} from "./Table";
import {ExportCSV} from "./ExportCSV";

function App() {

    const [wallets, setWallets] = useState([]);
    const [walletsAmount, setWalletsAmount] = useState(0);

    const createWallets = () => {
        const wallets = [];
        for (let i = 0; i < walletsAmount; i++) {
            const wallet = ethers.Wallet.createRandom();

            const walletObject = {
                address: wallet.address,
                mnemonic: wallet.mnemonic.phrase,
                privateKey: wallet.privateKey
            }

            wallets.push(walletObject);
        }
        setWallets(wallets);
    }

  return (
    <Container style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '100vh',
        gap: '20px',
        padding: '20px'
    }}>
        <h1>
            Wallets Maker App
        </h1>
        <h2>
            Wallets amount
        </h2>
        <input
            type="number"
            value={walletsAmount}
            onChange={e => setWalletsAmount(+e.target.value)}/>
        <Button onClick={createWallets}>
            Create wallets
        </Button>
        <ExportCSV csvData={wallets} fileName={"__MetaMask Wallets__ " + Date()}/>
        <WalletTable wallets={wallets}/>
    </Container>
  );
}

export default App;
