import Table from 'react-bootstrap/Table';
export const WalletTable = ({wallets}) => {
    const WalletRow = (wallet ,index) => {
        return(
            <tr key = {index} className='even'>
                <td> {index + 1} </td>
                <td>{wallet.address}</td>
                <td>{wallet.privateKey}</td>
                <td>{wallet.mnemonic}</td>
            </tr>
        )
    }
    const walletTable = wallets.map((wallet, index) => WalletRow(wallet, index))
    const tableHeader = <thead className='bgvi'>
                            <tr>
                                <th>#</th>
                                <th>ADDRESS</th>
                                <th>PRIVATE KEY</th>
                                <th>MNEMONIC</th>
                            </tr>
                        </thead>

    return (
        <Table striped bordered hover>
            {tableHeader}
            <tbody>
            {walletTable}
            </tbody>
        </Table>
    )
}